.container {
    background: $background_container_color;
    box-shadow: 0 2px 4px rgba(0, 0, 0, .2);
    position: relative;
    padding-left: 0;
    padding-right: 0;

    &:before, &:after {
        content: '';
        position: absolute;
        top: 100%;
        background: $background_container_color;
        box-shadow: 0 2px 4px rgba(0, 0, 0, .2);
        z-index: -1; }

    &:before {
        height: 10px;
        left: 4px;
        right: 4px; }

    &:after {
        height: 5px;
        left: 2px;
        right: 2px; } }
